.page-hero {
  padding-top: 153px;
  background-color: var(--color-athens);

  padding-bottom: 80px;
  min-height: 600px;

  background-position: right -10px top;
  background-repeat: no-repeat;

  &__title {
    margin-bottom: 23px;

    max-width: 600px;
    font-weight: 700;
    font-size: 72px;
    line-height: 130%;
    letter-spacing: 1px;
    text-transform: uppercase;

    color: var(--color-ebony);

    &--small {
      font-weight: 700;
      font-size: 64px;
      line-height: 120%;
      letter-spacing: 1px;
      text-transform: initial;
    }

    &--mb {
      margin-bottom: 60px;
    }

    &--post {
      max-width: initial;
      font-weight: 700;
      font-size: 64px;
      line-height: 120%;
      letter-spacing: 1px;
      text-transform: initial;

      color: var(--color-ebony);
      margin-bottom: 48px;
    }
  }

  &__descr {
    max-width: 600px;
    font-weight: 400;
    font-size: 20px;
    line-height: 150%;

    color: var(--color-storm);
  }

  &--services {
    background-image: url("../img/bg-image-services.png");
  }

  &--services-inner {
    background-image: url("../img/services-v2/bg-hero-services-v2.png");
  }

  &--work {
    background-image: url("../img/work/hero-work-bg.png");
  }

  &--work-inner {
    min-height: auto;
    background-image: url("../img/work-v2/bg-hero-work-v2.png");
    background-position: right -10px top 48px;
    background-color: var(--color-light);
    padding-bottom: 100px;
  }

  &--about {
    position: relative;
    background-image: url("../img/about/about-hero-image-1.png");
  }

  &--positions {
    background-image: url("../img/positions/hero-bg-positions.png");
    background-position: right -10px bottom;
  }

  &__image {
    position: absolute;
    top: 0;
    right: -9px;
    z-index: 2;
  }

  &--news {
    background-image: url("../img/news/hero-bg-news.png");
  }

  &--contacts {
    background-image: url("../img/contacts/hero-contacts-bg.png");
  }
}

.hero-socials {
  @include flex-v-center;

  &__item {
    &:not(:last-child) {
      margin-right: 12px;
    }
  }

  &__link {
    color: var(--color-storm);
    outline: 2px solid transparent;
    transition: outline-offset 0.3s ease-in-out, outline 0.3s ease-in-out;

    &:focus {
      .hero-socials__icon {
        outline-offset: 5px;
        outline: 2px solid var(--color-primary);
      }
    }

    &:hover {
      .hero-socials__icon {
        fill: var(--color-primary);
      }
    }

    &:active {
      .hero-socials__icon {
        fill: var(--color-active);
      }
    }
  }

  &__icon {
    display: block;
    width: 24px;
    height: 24px;
    object-fit: cover;
    fill: var(--color-storm);
    transition: fill 0.3s ease-in-out;
  }
}
