.categories {
  padding-top: 121px;
  background-color: var(--color-light);
  padding-bottom: 180px;

  &__title {
    margin-bottom: 38px;
  }
}

.categories-nav {
  @include flex;
  align-items: flex-start;
  margin-bottom: 61px;

  &__item {
    width: calc(100% / 6);
  }

  &__btn {
    @include flex-all-center;

    width: 100%;
    border: 1px solid transparent;
    font-weight: 700;
    font-size: 16px;
    text-transform: initial;
    color: var(--color-manate);

    padding: 12px 10px;
    transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;

    @include outline-primary-text;

    &:focus {
      outline-offset: 2px;
    }

    &--active {
      color: var(--color-primary);
      border-color: var(--color-primary);
    }
  }
}

.categories-list {
  grid-template-rows: repeat(3, 1fr);
  row-gap: 56px;
  margin-bottom: 55px;

  &__item {
    grid-column: 6 span;
  }

  &__article {
    height: auto;
  }
}

.categories-pag {
  @include flex-all-center;

  &__item {
    &:not(:last-child) {
      margin-right: 21px;
    }
  }

  &__arrow {
    display: block;
    width: 24px;
    height: 24px;
    object-fit: cover;
    fill: currentColor;
  }

  &__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;

    color: var(--color-gray);
    transition: color 0.3s ease-in-out;

    @include outline-primary-text;

    &--active {
      color: var(--color-primary);
    }
  }
}
