.custom-checkbox__input {
  position: absolute;
  opacity: 0;
  outline-offset: 0px;
  outline-color: transparent;
  transition: outline-offset 0.3s ease-in-out, outline-color 0.3s ease-in-out;
}
.custom-checkbox__text {
  display: inline-block;
  position: relative;
  font-weight: 400;
  font-size: 14px;
  line-height: 150%;
  padding-left: 27px;
  color: var(--color-gray);
  outline-offset: 0px;
  outline-color: transparent;
  transition: outline-offset 0.3s ease-in-out, outline-color 0.3s ease-in-out;
}
.custom-checkbox__text--contacts-us {
  max-width: 315px;
  font-weight: 400;
  font-size: 16px;
  line-height: 160%;
}
@media (max-width: 576px) {
  .custom-checkbox__text {
    font-size: 11px;
  }
}
.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: -1px;
  top: 3px;
  display: inline-block;
  border: 1px solid var(--color-primary);
  border-radius: 3px;
  width: 16px;
  height: 16px;
  transition: background-color 0.3s ease-in-out;
}
.custom-checkbox__text::after {
  content: "";
  position: absolute;
  left: -1px;
  top: 3px;
  width: 16px;
  height: 16px;
  background-image: url("../img/check.svg");
  background-position: center;
  background-size: 10px 8px;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.custom-checkbox__input:checked + .custom-checkbox__text::before {
  background-color: var(--color-primary);
}
.custom-checkbox__input:checked + .custom-checkbox__text::after {
  opacity: 1;
}
.custom-checkbox__input:focus + .custom-checkbox__text {
  outline-offset: 5px;
  outline: 1px solid var(--color-primary);
}

