.header {
  padding-top: 23px;
  padding-bottom: 21px;

  &__container {
    @include flex-all-sb;
  }

  &--main {
    color: var(--color-light);
  }

  &__nav {
    margin-left: -112px;

    @include minidesc {
      margin-left: 0;
    }

    @include tablet {
      display: none;
    }
  }

  &--secondary {
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 5;
  }

  .logo {
    line-height: 0;

    img {
      @include minidesc {
        max-width: 100px;
      }

      @include tablet {
        max-width: none;
      }

      @include mobile {
        max-width: 100px;
      }
    }
  }

  @include tablet {
    height: 65px;
  }

  @include mobile {
    height: 50px;
    padding-top: 17px;
  }
}
