.we-offer {
  padding-top: 120px;
  padding-bottom: 175px;
  background-color: var(--color-light);

  &__container {
    position: relative;
  }

  &__image {
    grid-column: 7 span;
    border-radius: 4px;
    overflow: hidden;

    max-width: 705px;
  }

  &__content {
    position: absolute;
    max-width: 376px;
    width: 100%;
    right: 134px;
    padding-top: 4px;
    padding-left: 75px;
    grid-column: 5 span;
    // margin-left: -30px;
  }

  &__title {
    font-family: "Lato", sans-serif;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
    letter-spacing: 0.6px;

    margin-bottom: 56px;
  }

  &__accordion {
    &:not(:last-child) {
      margin-bottom: 10px;
    }

    .accordion__control {
      position: relative;
      text-align: left;
      padding-left: 44px;

      @include outline-primary-text;
    }

    .accordion__title {
      position: relative;
      display: inline-block;

      font-weight: 700;
      font-size: 28px;
      line-height: 150%;
    }

    .accordion__icon {
      position: absolute;
      left: 0;
      top: 10px;
      display: block;
      width: 22px;
      height: 22px;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        top: 50%;
        transform: translateY(-50%);

        width: 100%;
        height: 2px;

        border-radius: 100px;
        background-color: var(--color-primary);
      }

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);

        width: 2px;
        height: 100%;

        border-radius: 100px;
        background-color: var(--color-primary);
        transition: opacity 0.3s ease-in-out;
      }
    }

    .accordion__content {
      display: inline-block;
      width: 376px;
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;

      color: var(--color-storm);
      padding-left: 44px;
      margin-top: -5px;
      padding-bottom: 10px;
    }
  }

  .is-open .accordion__icon {
    transform: none;

    &::after {
      opacity: 0;
    }
  }
}
